<template>
  <div class="home">
    <div class="title">
      <div class="container">
      <!-- title parameter passed through the parent element -->
      <h1>{{ title }}</h1>
      <p>{{datetime}} 기준 공고중 리스트를 확인해보자.</p>
      <a>Made by KBS. 2022.</a>
      </div>

      <img class="wave" src="./../assets/images/wavewhite.svg">
    </div>
    
    <div class="answer">
            <!-- v-model input에서 값을 받아올 이름 지정 -->
            <div class="select-dropdown">
                <select v-model="selected" @change="msgBox">
                    <option v-for="loc in loc_list" :value="loc" selected>{{loc}}</option>
                </select>
            </div>
    </div>

      <div class="container-table">
        <div class="table-wrapper">
          <table class="fl-table">
            <thead>
              <tr>
                <th scope="col">#</th>
                <th scope="col">공고상태</th>
                <th scope="col">지역</th>
                <th scope="col">공고유형</th>
                <th scope="col">공고대상</th>
                <th scope="col">공고명</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(line, index) in result" :key="index">
                <th scope="row">{{ index + 1 }}</th>
                <td>{{ line.PAN_SS }}</td>
                <td>{{ line.CNP_CD_NM }}</td>
                <td>{{ line.UPP_AIS_TP_NM }}</td>
                <td>{{ line.AIS_TP_CD_NM }}</td>
                <td><a v-bind:href=line.DTL_URL>{{ line.PAN_NM }}</a></td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>

  </div>
</template>

<script>
import axios from 'axios';
import dotenv from 'dotenv';

dotenv.config()

function leftPad(value) {
    if (value >= 10) {
        return value;
    }

    return `0${value}`;
}

let date = new Date();

let CLSG_DT = date.getFullYear() + '.' + leftPad(date.getMonth() + 1) + '.' + leftPad(date.getDate());

let PAN_NT_ST_DT = date.getFullYear() + '.' + leftPad(date.getMonth()) + '.' + leftPad(date.getDate());

export default {
  name: 'Start',
  // "state" of the component, for proper state management for multiple components
  // have a look at vuex
  data () {
    return {
      result: '',
      api_url: 'https://apis.data.go.kr/B552555/lhLeaseNoticeInfo1/lhLeaseNoticeInfo1',
      api_params: {
          'ServiceKey' : process.env.VUE_APP_API_KEY,
          'PG_SZ' : '50', 
          'PAGE' : '1', 
          'PAN_NM' : '서울',
          'PAN_SS' : '공고중',
          'PAN_NT_ST_DT' : PAN_NT_ST_DT, 
          'CLSG_DT' : CLSG_DT
      },
      datetime:CLSG_DT,
      loc_list: [
        '서울', '경기', '부산', '인천', '대구', '대전', '광주', '울산', '세종', '강원', '충북', '충남', '전북', '전남', '경북', '경남', '제주'
      ],
      loc_select: '',
      selected:'서울'
    }
  },
  // Declaration of parameters passed through the parent element
  props: {
    title: String
  },
  // methods that can be called
  methods: {
    /**
    * Calls the yes/no API with axios and saves that into the data.
    */
    loadData: function() {
      axios.get(this.api_url, {params: this.api_params}) // returns a promise
        .then(response => {
          // axios returns a response object, which contains the requested data
          // can be accessed via response.data
          this.result = response.data[1]['dsList']
        }).catch(err => {
          alert('Error while trying to call the api: ' + err)
        })
    },
    msgBox: function() {
      this.api_params['PAN_NM'] = this.selected
      this.loadData()
    }
  },
  // vue-hook that gets called once the component is mounted
  mounted() {
    this.loadData()
  }
}
</script>

